import { NotificationService } from "../notificationService";
import { MatchingService } from "../matchingService";
import { Pet, IPet } from "../../models/Pet";
import { User, IUser } from "../../models/User";
import {
  NotificationType,
  NotificationPriority,
} from "../../models/Notification";
import { logger } from "../../utils/logger";

export interface AIMatchingConfig {
  enabled: boolean;
  minSimilarity: number;
  checkInterval: number; // in minutes
  maxDistance: number; // in kilometers
}

export class AIMatchingService {
  private static config: AIMatchingConfig = {
    enabled: true,
    minSimilarity: 0.75,
    checkInterval: 30, // 30 minutes
    maxDistance: 50, // 50km
  };

  private static interval: NodeJS.Timeout | null = null;
  private static lastRun: Date | null = null;

  /**
   * ÂàùÂßãÂåñ AI ÈÖçÂ∞çÊúçÂãô
   */
  static initialize(config?: Partial<AIMatchingConfig>): void {
    if (config) {
      this.config = { ...this.config, ...config };
    }

    this.start();
    logger.info("AI ÈÖçÂ∞çÊúçÂãôÂ∑≤ÂàùÂßãÂåñ", { config: this.config });
  }

  /**
   * ÂïüÂãï AI ÈÖçÂ∞ç‰ªªÂãô
   */
  static start(): void {
    if (!this.config.enabled || this.interval) return;

    const intervalMs = this.config.checkInterval * 60 * 1000;

    this.interval = setInterval(async () => {
      try {
        await this.processAIMatching();
      } catch (error) {
        logger.error("AI ÈÖçÂ∞çÈÄöÁü•‰ªªÂãôÂ§±Êïó", { error });
      }
    }, intervalMs);

    logger.info("AI ÈÖçÂ∞çÈÄöÁü•‰ªªÂãôÂ∑≤ÂïüÂãï", {
      interval: this.config.checkInterval,
    });
  }

  /**
   * ÂÅúÊ≠¢ AI ÈÖçÂ∞ç‰ªªÂãô
   */
  static stop(): void {
    if (this.interval) {
      clearInterval(this.interval);
      this.interval = null;
      logger.info("AI ÈÖçÂ∞çÈÄöÁü•‰ªªÂãôÂ∑≤ÂÅúÊ≠¢");
    }
  }

  /**
   * ËôïÁêÜ AI ÈÖçÂ∞çÈÄöÁü•
   */
  private static async processAIMatching(): Promise<void> {
    try {
      this.lastRun = new Date();

      // Áç≤ÂèñÊúÄËøë 24 Â∞èÊôÇÂÖßÊñ∞Â¢ûÁöÑÂØµÁâ©
      const yesterday = new Date(Date.now() - 24 * 60 * 60 * 1000);

      const recentPets = await Pet.find({
        createdAt: { $gte: yesterday },
        status: { $in: ["lost", "found"] },
        "aiData.features": { $exists: true, $ne: null },
      }).populate("owner");

      for (const pet of recentPets) {
        try {
          await this.processSinglePetAIMatching(pet);
        } catch (error) {
          logger.error("ËôïÁêÜÂØµÁâ© AI ÈÖçÂ∞çÂ§±Êïó", {
            petId: pet._id,
            error,
          });
        }
      }
    } catch (error) {
      logger.error("AI ÈÖçÂ∞çËôïÁêÜÂ§±Êïó", { error });
    }
  }

  /**
   * ËôïÁêÜÂñÆÂÄãÂØµÁâ©ÁöÑ AI ÈÖçÂ∞ç
   */
  static async processSinglePetAIMatching(pet: IPet): Promise<{
    matchesFound: number;
    notificationsSent: number;
  }> {
    try {
      const potentialMatches = await MatchingService.findPotentialMatches(
        pet._id.toString(),
        {
          minSimilarity: this.config.minSimilarity,
          maxDistance: this.config.maxDistance,
          maxDays: 1, // Âè™Ê™¢Êü•ÊúÄËøë‰∏ÄÂ§©
        },
      );

      if (potentialMatches.length > 0) {
        // ÁôºÈÄÅ AI ÈÖçÂ∞çÈÄöÁü•
        await this.sendAIMatchNotification(pet, potentialMatches);
        return {
          matchesFound: potentialMatches.length,
          notificationsSent: 1,
        };
      }

      return {
        matchesFound: 0,
        notificationsSent: 0,
      };
    } catch (error) {
      logger.error("ËôïÁêÜÂØµÁâ© AI ÈÖçÂ∞çÂ§±Êïó", {
        petId: pet._id,
        error,
      });
      throw error;
    }
  }

  /**
   * ÁôºÈÄÅ AI ÈÖçÂ∞çÈÄöÁü•
   */
  private static async sendAIMatchNotification(
    pet: IPet,
    matches: any[],
  ): Promise<void> {
    const owner = (await User.findById(pet.userId)) as IUser;
    const bestMatch = matches[0];
    const similarity = Math.round(bestMatch.similarity * 100);

    await NotificationService.sendNotification({
      userId: owner._id.toString(),
      type: "AI_MATCH_SUGGESTION" as NotificationType,
      title: "ü§ñ AI ÁôºÁèæÊΩõÂú®ÈÖçÂ∞ç",
      message: `AI ÁÇ∫ÊÇ®ÁöÑ${pet.status === "lost" ? "Â§±Ëπ§" : "ÊãæÁç≤"}ÂØµÁâ© ${pet.name} ÊâæÂà∞‰∫Ü ${matches.length} ÂÄãÊΩõÂú®ÈÖçÂ∞çÔºåÊúÄÈ´òÁõ∏‰ººÂ∫¶ ${similarity}%`,
      data: {
        petId: pet._id.toString(),
        matchCount: matches.length,
        bestSimilarity: similarity,
        matches: matches.slice(0, 3).map((m) => ({
          petId: pet.status === "lost" ? m.foundPet._id : m.lostPet._id,
          similarity: Math.round(m.similarity * 100),
          distance: Math.round(m.distance || 0),
        })),
      },
      priority: NotificationPriority.HIGH,
    });
  }

  /**
   * ÊâãÂãïËß∏Áôº AI ÈÖçÂ∞çÊ™¢Êü•
   */
  static async triggerAIMatching(
    userId: string,
    petId?: string,
  ): Promise<{
    matchesFound: number;
    notificationsSent: number;
    message: string;
  }> {
    try {
      let pets: IPet[];

      if (petId) {
        const pet = await Pet.findOne({ _id: petId, owner: userId });
        if (!pet) {
          throw new Error("ÂØµÁâ©‰∏çÂ≠òÂú®ÊàñÁÑ°Ê¨äÈôê");
        }
        pets = [pet];
      } else {
        pets = await Pet.find({
          owner: userId,
          status: { $in: ["lost", "found"] },
        }).populate("owner");
      }

      let totalMatches = 0;
      let totalNotifications = 0;

      for (const pet of pets) {
        const result = await this.processSinglePetAIMatching(pet);
        totalMatches += result.matchesFound;
        totalNotifications += result.notificationsSent;
      }

      return {
        matchesFound: totalMatches,
        notificationsSent: totalNotifications,
        message: `Â∑≤Ê™¢Êü• ${pets.length} ÈöªÂØµÁâ©ÔºåÊâæÂà∞ ${totalMatches} ÂÄãÊΩõÂú®ÈÖçÂ∞çÔºåÁôºÈÄÅ ${totalNotifications} ÂÄãÈÄöÁü•`,
      };
    } catch (error) {
      logger.error("ÊâãÂãïËß∏Áôº AI ÈÖçÂ∞çÂ§±Êïó", { userId, petId, error });
      throw error;
    }
  }

  /**
   * Áç≤ÂèñÈÖçÁΩÆ
   */
  static getConfig(): AIMatchingConfig {
    return { ...this.config };
  }

  /**
   * Êõ¥Êñ∞ÈÖçÁΩÆ
   */
  static updateConfig(newConfig: Partial<AIMatchingConfig>): AIMatchingConfig {
    this.config = { ...this.config, ...newConfig };

    // Â¶ÇÊûúÂïüÁî®ÁãÄÊÖãÊîπËÆäÔºåÈáçÊñ∞ÂïüÂãïÊàñÂÅúÊ≠¢ÊúçÂãô
    if (newConfig.enabled !== undefined) {
      this.stop();
      if (newConfig.enabled) {
        this.start();
      }
    }

    logger.info("AI ÈÖçÂ∞çÈÖçÁΩÆÂ∑≤Êõ¥Êñ∞", { config: this.config });
    return { ...this.config };
  }

  /**
   * Áç≤ÂèñÊúÄÂæåÈÅãË°åÊôÇÈñì
   */
  static getLastRun(): Date | null {
    return this.lastRun;
  }

  /**
   * Áç≤ÂèñÈÅãË°åÁãÄÊÖã
   */
  static getStatus(): {
    enabled: boolean;
    running: boolean;
    lastRun: Date | null;
    config: AIMatchingConfig;
  } {
    return {
      enabled: this.config.enabled,
      running: this.interval !== null,
      lastRun: this.lastRun,
      config: { ...this.config },
    };
  }
}
